import React from 'react';
import { Table } from 'react-bootstrap';

const ReservationsTable = ({reservations}) => {
    return (
        <Table className="table-reservations" responsive striped bordered hover>
            <thead>
                <tr>
                <th>First Name</th>
                <th>Last Name</th>
                <th>Phone number</th>
                <th>Email</th>
                <th>Date</th>
                <th>Hour</th>
                <th>Total people</th>
                </tr>
            </thead>
            <tbody>
                {reservations.map(r => 
                    <tr key={r.id}>
                        <td>{r.firstName}</td>
                        <td>{r.lastName}</td>
                        <td>{r.phoneNumber}</td>
                        <td>{r.email}</td>
                        <td>{r.date}</td>
                        <td>{r.hour}</td>
                        <td>{r.totalPeople}</td>
                    </tr>
                )}
            </tbody>
        </Table>
    )
}

export default ReservationsTable;