import React from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import './AboutPage.css'

const AboutPage = () => {

    document.title = "MaxRestaurant | About us"

    return(
        <div>
            <Container fluid className="about-container">
                <Row>
                    <Col xl={6} md={12} xs={12} className="col-about">
                        <p className="about-info">           
                            This page was created by <a className="link-about" href="https://www.linkedin.com/in/leonelzorzoli/" rel="noreferrer" target="_blank">Leonel Zorzoli</a> with 
                            the aim of improving his skills as a web programmer.
                            Everything you see here is fictional and created for demonstration purposes.
                            All the images were taken from Google Images and the entire menu is from <a className="link-about" href="https://earls.ca/">Earls</a> restaurant.
                        </p>
                        <p className="about-info">
                            Created with React, React-Redux, React-Router, Bootstrap, Node, Express, MongoDB
                        </p>
                    </Col>
                    <Col xl={6} md={12} xs={12} className="col-about">
                        <p className="personal-info">
                            I'm a 25-year-old guy based in Argentina, passionate about creating products that help the community, 
                            and solving real world problems with software. I like to be in the intersection of design, marketing and engineering. 
                        </p>
                        <p className="social">
                            You can find me on github as <a className="link-about" href="https://github.com/LeoZorzoli" rel="noreferrer" target="_blank">@LeoZorzoli</a> or 
                            on twitter as <a className="link-about" href="https://twitter.com/ProgramandoM" rel="noreferrer" target="_blank">@ProgramandoM</a>
                        </p>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default AboutPage