import menuService from '../services/menu'

export const getMenu = () => {
    return async dispatch => {
        const menu = await menuService.getAll()
        dispatch({
            type: 'INIT_MENU',
            data: menu
        })
    }
}

const menuReducer = (state = [], action) => {
    switch(action.type){
        case 'INIT_MENU':
            return action.data
        default:
            return state
    }
}

export default menuReducer