import React, { useState, useEffect } from 'react'
import { Navbar, Nav } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'

import './Navbar.css'

const NavbarComponent = () => {
    const [expanded, setExpanded] = useState(false)
    const [scroll, setScroll] = useState(false)

    useEffect(() => {
        window.addEventListener("scroll", () => {
            setScroll(window.scrollY > 50);
        })
    }, [])

    return (
        <Navbar expanded={expanded} fixed="top" expand="lg" className={scroll ? "a-nav nav-shadow" : "a-nav"}>
            <Navbar.Toggle onClick={() => setExpanded(expanded ? false : 'expanded')} aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
                    <NavLink onClick={() => setExpanded(false)} className="nav-link logo" to="/">Max<span className="restaurant">Restaurant</span></NavLink>
                <Nav className="mr-auto">
                    <NavLink onClick={() => setExpanded(false)} to="/menu" className="nav-link link-style">Menu</NavLink>
                    <NavLink onClick={() => setExpanded(false)} to="/about" className="nav-link link-style">About</NavLink>
                </Nav>
                <Nav>
                    <NavLink onClick={() => setExpanded(false)} to="/reservations" className="nav-link link-style">Reservations</NavLink>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default NavbarComponent