import React from 'react'
import { Container, Row, Col} from 'react-bootstrap'

import './Menu.css'

const Item = (item) => {
    const i = item.item
    return(
        <div className="item-div">
            <div>
                <p className="item-title"><span>{i.name}</span><span className="right">{i.price}</span></p>
            </div>

            <p className="item-description"><small >{i.description}</small></p>
                {i.additions.map(a => <p key={a._id} className="additions"><small>{a.name}</small><span className="right-additions">{a.price}</span></p>)}
        </div>
    )
}

const Type = (type) => {
    const t = type.type

    return(
        <div className="type-div">
            <div className="flex">
                <p className="subtitle"><strong>{t.name}</strong></p>
            </div>
            <Row>
                {t.items.map(i =>
                    <Col key={i._id} xs={12} md={4}>
                        <Item item={i} />
                    </Col>
                )}
            </Row>
        </div>
    )
}

const Menu = (m) => {
    return (
        <Container fluid className="container-menu-component">
            <p className="title-menu fancy"><span><strong>{m.menu.name}</strong></span></p>
            <Row>
                {m.menu.types.map(type => <Type key={type._id} type={type} />)}
            </Row>
        </Container>
    )
}

export default Menu