import React from 'react'
import { useDispatch } from 'react-redux'
import { Form, Button, Container, Row, Col } from 'react-bootstrap'
import { newReservation } from '../reducers/reservationsReducer'
import { useSelect, useField } from '../hooks/index'
import { createNotification } from '../reducers/notificationReducer'
import Notification from '../components/Notification/Notification'

import './ReservationPage.css'

const ReservationsPage = () => {

    document.title = "MaxRestaurant | Reservate"

    const dispatch = useDispatch()

    const totalPeople = useSelect()
    const hour = useSelect()
    const date = useField('date')
    const firstName = useField('text')
    const lastName = useField('text')
    const phone = useField('text')
    const email = useField('email')

    const addReservation = (event) => {
        event.preventDefault()
        if (totalPeople.selected === null || hour.selected === null || date.value === null 
            || firstName.value === null || lastName.value === null || email.value === null)
            {
                dispatch(createNotification('Missing data', 2))

            } else {
                const reservationObject = {
                    totalPeople: totalPeople.selected,
                    hour: hour.selected,
                    date: date.value,
                    firstName: firstName.value,
                    lastName: lastName.value,
                    phoneNumber: phone.value,
                    email: email.value
                }
        
                dispatch(newReservation(reservationObject))
                dispatch(createNotification('Reservation created', 2))
            }
    }

    return(
        <div className="reservation-container">
            <Container>
                <Form onSubmit={addReservation}>
                    <Row className="row-form">
                        <Col xs={12} md={4}>
                            <Form.Group controlId="totalPeople">
                                <Form.Label className="label-reservation">Total People</Form.Label>
                                <Form.Control {...totalPeople} className="form-control-reservation" as="select"  defaultValue='Choose...'>
                                    <option disabled>Choose...</option>
                                    <option>1</option>
                                    <option>2</option>
                                    <option>3</option>
                                    <option>4</option>
                                    <option>5</option>
                                    <option>6</option>
                                    <option>7</option>
                                    <option>8</option>
                                </Form.Control>
                                <Form.Text className="text-reservation">
                                    To reserve a table for more than 8 people call <span className="phone-number">1500-777-0800</span>.
                                </Form.Text>
                            </Form.Group>
                        </Col>
                        <Col xs={12} md={4}>
                            <Form.Group controlId="date">
                                <Form.Label className="label-reservation">Date</Form.Label>
                                <Form.Control {...date} className="form-control-reservation"/>
                                <Form.Text className="text-reservation">
                                    You have up to three days in advance to change the date.
                                </Form.Text>
                            </Form.Group>
                        </Col>
                        <Col xs={12} md={4}>
                            <Form.Group controlId="hour">
                                <Form.Label className="label-reservation">Hour</Form.Label>
                                <Form.Control {...hour} className="form-control-reservation" as="select" defaultValue="Choose...">
                                    <option disabled>Choose...</option>
                                    <option>9:00</option>
                                    <option>9:30</option>
                                    <option>10:00</option>
                                    <option>10:30</option>
                                    <option>11:00</option>
                                    <option>11:30</option>
                                    <option>12:00</option>
                                    <option>12:30</option>
                                    <option>13:00</option>
                                    <option>13:30</option>
                                    <option>18:00</option>
                                    <option>18:30</option>
                                    <option>19:00</option>
                                    <option>19:30</option>
                                    <option>20:00</option>
                                    <option>20:30</option>
                                    <option>21:00</option>
                                    <option>21:30</option>
                                    <option>22:00</option>
                                    <option>22:30</option>
                                    <option>23:00</option>
                                    <option>23:30</option>
                                </Form.Control>
                                <Form.Text className="text-reservation">
                                    You have up to three days in advance to change the hour.
                                </Form.Text>
                            </Form.Group>
                        </Col>
                    </Row>
                    <div className="personal-info2">
                        <p className="subtitle">Personal Info</p>
                        <Row className="row-form">
                            <Col xs={12} md={6}>
                                <Form.Group controlId="firstName">
                                    <Form.Label className="label-reservation">First Name</Form.Label>
                                    <Form.Control {...firstName} className="form-control-reservation"/>
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={6}>
                                <Form.Group controlId="lastName">
                                    <Form.Label className="label-reservation">Last Name</Form.Label>
                                    <Form.Control {...lastName} className="form-control-reservation" />
                                </Form.Group>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={12} md={6}>
                                <Form.Group controlId="phone">
                                    <Form.Label className="label-reservation">Phone</Form.Label>
                                    <Form.Control {...phone} className="form-control-reservation" />
                                    <Form.Text className="text-reservation">
                                        Only for trouble reports.
                                    </Form.Text>
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={6}>
                                <Form.Group controlId="email">
                                    <Form.Label className="label-reservation">Email</Form.Label>
                                    <Form.Control {...email} className="form-control-reservation" />
                                    <Form.Text className="text-reservation">
                                        In case of problems with the reservation we will contact this email.
                                    </Form.Text>
                                </Form.Group>
                            </Col>
                        </Row>
                    </div>
                    <div className="btn-middle">
                        <Button className="button-reservation" type="submit">
                            Submit
                        </Button>
                    </div>
                </Form>
            </Container>
            <Notification />
        </div>
    )
}

export default ReservationsPage